/* src/dog.css */
/* === Dog Park theme (body.theme-dog) ===================================== */
@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@600;800&display=swap");

:root {
  --dog-navy: #08111c;
  --dog-deep: #0e1a2a;
  --dog-cyan: #35d0ff;
  --dog-yellow: #ffe27a;
  --dog-pink: #ff6b8b;
  --dog-lime: #7ef29a;
  --dog-teal: #29c7a9;
  --dog-ink: #e8f0ff;
}

body.theme-dog {
  color: var(--dog-ink);
  font-family: "Nunito", system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
  background-image: url("./assets/dog-bg.jpg"); /* overlay removed */
  background-repeat: repeat;
  background-size: 720px auto;
  background-position: 0 0;
}

body.theme-dog .hud,
body.theme-dog .chrome-card {
  background: rgba(8,17,28,.78);
  border: 1px solid rgba(255,255,255,.10);
  border-radius: 16px;
  padding: 14px;
  box-shadow: 0 12px 28px rgba(0,0,0,.35);
  backdrop-filter: blur(3px);
}

body.theme-dog .app-title {
  margin: 0;
  color: var(--dog-yellow);
  text-shadow: 0 2px 0 rgba(0,0,0,.35);
  letter-spacing: .3px;
  font-weight: 800;
}

body.theme-dog .version-text { color: #c6eeff; opacity: .9; }

body.theme-dog .theme-switch .theme-btn {
  color: var(--dog-ink);
  background: #0f2136;
  border: 1px solid #2a4266;
  border-radius: 10px;
  padding: 8px 12px;
}
body.theme-dog .theme-switch .theme-btn.is-active {
  box-shadow: 0 0 0 2px var(--dog-yellow) inset;
  background: rgba(255,226,122,.12);
}

body.theme-dog .legend-swatch { border-color: rgba(255,255,255,.15); }
body.theme-dog .legend-red    { background: rgba(255,107,139,.65); }
body.theme-dog .legend-yellow { background: rgba(255,226,122,.65); }

body.theme-dog .stat-chip {
  background: rgba(14,26,42,.78);
  border: 1px solid rgba(255,255,255,.10);
  color: var(--dog-ink);
}

/* Editor & decorations */
body.theme-dog .editor-content { color: var(--dog-ink); }
body.theme-dog .editor-content [data-highlight-color="yellow"] {
  background-color: rgba(255,226,122,.35);
  border: 1px solid #ffd463;
  border-radius: 6px; padding: 0 .08em;
}
body.theme-dog .editor-content [data-highlight-color="red"] {
  background-color: rgba(255,107,139,.35);
  border: 1px solid #ff6b8b;
  border-radius: 6px; padding: 0 .08em;
}
body.theme-dog .editor-content [data-hard-word="1"] {
  text-decoration: none;
  border-bottom: 2px dotted #cfa0ff;
  padding-bottom: 1px;
}

/* Tooltip */
body.theme-dog #ss-tooltip {
  background: #0b1626 !important;
  color: #e9f3ff !important;
  border: 1px solid #2a4266 !important;
  border-radius: 10px !important;
  box-shadow: 0 10px 24px rgba(0,0,0,.35) !important;
}
