/* === Catnap Theme (enabled when <body> has .theme-cat) =================== */
/* Friendly rounded font */
@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@600;800&display=swap");

:root {
  --cat-cream: #fff7ec;
  --cat-peach: #ffd9c2;
  --cat-caramel: #f4b183;
  --cat-cocoa: #5b4636;
  --cat-ink: #27303a;
  --cat-mint: #c9f1e5;
  --cat-salmon: #ff9aa2;
  --cat-yellow: #ffe59a;
  --cat-red: #ffb3b3;
  --cat-blue: #cfe9ff;
}

/* Background: soft gradients + your tile */
body.theme-cat {
  color: var(--cat-ink);
  font-family: "Nunito", system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;

  background-image:
    radial-gradient(1200px 600px at -20% -10%, rgba(255,217,194,.35), transparent 60%),
    radial-gradient(900px 500px at 110% 10%, rgba(201,241,229,.35), transparent 60%),
    url("./assets/cat-bg.png");
  background-repeat: no-repeat, no-repeat, repeat;
  background-size: cover, cover, 420px 210px; /* adjust last size to scale the tile */
  background-position: center top, center top, 0 0;
}

/* Readable “chrome” on patterned background */
body.theme-cat .hud {
  background: rgba(255,247,236,.88);
  border: 1px solid #eddcc8;
  border-radius: 16px;
  padding: 14px;
  margin: 10px 0 14px;
  box-shadow: 0 10px 24px rgba(91,70,54,.08);
  backdrop-filter: blur(4px);
}

body.theme-cat .chrome-card {
  background: rgba(255,247,236,.92);
  border: 1px solid #eddcc8;
  border-radius: 16px;
  padding: 14px;
  box-shadow: 0 10px 24px rgba(91,70,54,.08);
}

/* Title with tiny cat ears */
body.theme-cat .app-title {
  color: var(--cat-cocoa);
  font-weight: 800;
  letter-spacing: .2px;
  position: relative;
  display: inline-block;
  padding-top: 6px;
  margin: 0;
}
body.theme-cat .app-title::before,
body.theme-cat .app-title::after {
  content: "";
  position: absolute;
  top: -8px;
  width: 14px;
  height: 14px;
  background: var(--cat-peach);
  border: 2px solid var(--cat-cocoa);
  border-bottom: none;
  transform: rotate(45deg);
  border-radius: 2px 2px 0 0;
}
body.theme-cat .app-title::before { left: -18px; }
body.theme-cat .app-title::after  { right: -18px; }

/* Plain version text */
body.theme-cat .version-text {
  margin-top: 4px;
  color: #6b7280;
  font-weight: 700;
}

/* Controls */
body.theme-cat select,
body.theme-cat .theme-switch .theme-btn,
body.theme-cat .btn,
body.theme-cat button {
  color: var(--cat-ink);
  background: #ffffff;
  border: 2px solid var(--cat-caramel);
  border-radius: 12px;
  padding: 8px 12px;
  transition: transform .05s ease, box-shadow .12s ease;
  box-shadow: 0 2px 0 rgba(91,70,54,.15);
}

/* Fix dropdown text clipping (Grade 6) */
body.theme-cat select { padding: 10px 12px; line-height: 1.2; }

/* Hover */
body.theme-cat button:hover,
body.theme-cat .btn:hover,
body.theme-cat select:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 14px rgba(91,70,54,.12);
}

/* Active theme button */
body.theme-cat .theme-switch .theme-btn.is-active {
  box-shadow: 0 0 0 2px var(--cat-caramel) inset;
  background: rgba(244,177,131,.12);
}

/* Stats chips */
body.theme-cat .stat-chip {
  background: var(--cat-cream);
  border: 1px solid #eddcc8;
  color: var(--cat-ink);
  border-radius: 999px;
}

/* Legend */
body.theme-cat .legend-row,
body.theme-cat .top-controls { gap: 10px; }

body.theme-cat .legend-swatch {
  width: 14px;
  height: 14px;
  border-radius: 4px;
  display: inline-block;
  border: 1px solid #e6d5c1;
}
body.theme-cat .legend-yellow { background: var(--cat-yellow); }
body.theme-cat .legend-red    { background: var(--cat-red); }
body.theme-cat .legend-blue   { background: var(--cat-blue); }

/* Hard word legend sample */
body.theme-cat .legend-underline-purple {
  text-decoration: underline dotted #c47dff;
  text-underline-offset: 3px;
  font-weight: 800;
}

/* Tooltip */
body.theme-cat #ss-tooltip {
  background: #fffdf9 !important;
  color: var(--cat-ink) !important;
  border: 1px solid #eddcc8 !important;
  border-radius: 10px !important;
  box-shadow: 0 10px 20px rgba(91,70,54,.12) !important;
}

/* Editor surface & highlights (target the decoration attributes) */
body.theme-cat .editor-content,
body.theme-cat .help-card,
body.theme-cat .panel,
body.theme-cat .box {
  background: var(--cat-cream);
  border: 1px solid #eddcc8;
  border-radius: 14px;
  box-shadow: 0 10px 24px rgba(91,70,54,.08);
  color: var(--cat-ink);
}

/* Yellow / Red “hardness” highlights */
body.theme-cat .editor-content [data-highlight-color="yellow"] {
  background-color: rgba(255,229,154,.6);
  color: #2a2a2a;
  border: 1px solid #f4d67e;
  border-radius: 6px;
  padding: 0 .08em;
}
body.theme-cat .editor-content [data-highlight-color="red"] {
  background-color: rgba(255,179,179,.6);
  color: #2a2a2a;
  border: 1px solid #f3a3a3;
  border-radius: 6px;
  padding: 0 .08em;
}
/* If you ever reintroduce a “blue/long” state, uncomment:
body.theme-cat .editor-content [data-highlight-color="blue"] {
  background-color: rgba(207,233,255,.55);
  color: #2a2a2a;
  border: 1px solid #b8daf7;
  border-radius: 6px;
  padding: 0 .08em;
}
*/

/* Purple underline for likely hard words (stays visible above highlight) */
body.theme-cat .editor-content [data-hard-word="1"] {
  text-decoration: none;
  border-bottom: 2px dotted #c47dff;
  padding-bottom: 1px;
}

/* Help accordion pretties */
body.theme-cat .help-summary {
  background: #fff1e7;
  color: var(--cat-cocoa);
  font-weight: 800;
  border-bottom: 1px solid #f5e2d0;
}
body.theme-cat .help-pre {
  background: #fffdf9;
  color: #1f2937;
  border-color: #eddcc8;
  border-radius: 10px;
}

/* Focus ring */
body.theme-cat *:focus-visible {
  outline: 2px solid var(--cat-caramel);
  outline-offset: 2px;
}
