/* src/custom.css */

/* --- App shell basics --- */
.app-shell { padding: 16px; }

/* Title & version */
.app-title { margin: 0 0 6px 0; font-weight: 800; font-size: 28px; }
.version-text { color: #6b7280; font-weight: 600; margin-bottom: 8px; }

/* HUD / chrome containers */
.hud { padding: 12px; border: 1px solid #e5e7eb; border-radius: 10px; background: #fff; margin-bottom: 12px; }
.chrome-card { padding: 12px; border: 1px solid #e5e7eb; border-radius: 12px; background: #fff; }

/* Layout rows */
.hud-row { display: flex; align-items: center; justify-content: space-between; gap: 16px; }
.hud-left { display: flex; flex-direction: column; }
.hud-right { display: flex; align-items: center; gap: 8px; }

.top-controls { display: flex; align-items: center; gap: 14px; flex-wrap: wrap; margin-top: 8px; }
.label { font-weight: 700; }

.theme-switch .theme-btn {
  border: 1px solid #d1d5db; background: #fff; padding: 6px 12px; border-radius: 10px; cursor: pointer;
}
.theme-switch .theme-btn.is-active { box-shadow: 0 0 0 2px #111827 inset; }

/* Legend */
.legend-row { display: flex; align-items: center; gap: 10px; flex-wrap: wrap; font-size: 0.95rem; }
.legend-swatch { width: 12px; height: 12px; border-radius: 3px; display: inline-block; border: 1px solid #e5e7eb; }
.legend-red { background: #fecaca; }
.legend-yellow { background: #fef3c7; }

/* Stats chips */
.stats-row { display: flex; gap: 8px; margin-top: 10px; }
.stat-chip { background: #f3f4f6; padding: 6px 10px; border-radius: 8px; }

/* Editor */
.editor-content {
  min-height: 220px;
  padding: 12px 14px;
  line-height: 1.55;
  outline: none;
}

/* Decorations (IMPORTANT: your highlighter uses <span> with these data attrs) */
.editor-content [data-highlight-color="yellow"] {
  background-color: #fff3bf;
  border: 1px solid #ffe066;
  border-radius: 6px;
  padding: 0 .08em;
}
.editor-content [data-highlight-color="red"] {
  background-color: #ffc9c9;
  border: 1px solid #ffa8a8;
  border-radius: 6px;
  padding: 0 .08em;
}
/* If you ever re-enable a "long sentence" blue: */
/* .editor-content [data-highlight-color="blue"] { background-color:#dbeafe; border:1px solid #bfdbfe; } */

.editor-content [data-hard-word="1"] {
  text-decoration: none;
  border-bottom: 2px dotted #7c3aed;
  padding-bottom: 1px;
}

/* Form controls */
select { line-height: 1.2; padding: 8px 12px; border-radius: 8px; border: 1px solid #d1d5db; }

/* Help accordion (unmodified from earlier good state) */
.help-card { border: 1px solid #e5e7eb; border-radius: 8px; background: #ffffff; overflow: hidden; margin-top: 14px; }
.help-summary { cursor: pointer; padding: 12px 14px; font-weight: 700; list-style: none; background: #f9fafb; }
.help-card[open] .help-summary { border-bottom: 1px solid #e5e7eb; }
.help-body { padding: 14px; }
.help-section + .help-section { margin-top: 16px; }
.help-section h3 { font-size: 1.05rem; font-weight: 700; margin: 0 0 6px; }
.help-pre { background: #f3f4f6; border: 1px solid #e5e7eb; border-radius: 6px; padding: 10px; white-space: pre-wrap; font-family: ui-monospace, SFMono-Regular, Menlo, Consolas, "Courier New", monospace; font-size: 0.92rem; line-height: 1.35; margin: 8px 0; }
.help-list { list-style: disc; padding-left: 1.25rem; margin: 0; }
.help-list li + li { margin-top: 4px; }

/* Arcade theme stat chips (dark background) */
body.theme-arcade .stat-chip { background: rgba(255,255,255,.10); color: #e2e8f0; border: 1px solid rgba(255,255,255,.12); }
